:root {
  --clr-primary-300: rgba(0, 0, 0, 0.35);
  --clr-primary-400: rgba(0, 0, 0, 0.45);
  --clr-primary-500: rgba(0, 0, 0, 0.55);

  --clr-nuetral-100:#fff;
  --clr-nuetral-900:#222C2A;

  --ff--primary:"Lato";
  --ff--accent:"Poppins";
}

*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  font-family: var(--ff--primary);
  font-weight: 400;
  font-size:1.3125rem;
  line-height:1.6
}
body,
h1,
h2{
}

h3{
  line-height:1;
  font-weight: 900;
  font-family:var(--ff--accent)
}

.text-center {
  text-align: center;
}

p {
  margin: 0;
}
header,
section {
  padding: 3rem 0;
}

.containerCenter{
  display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	justify-content: center;
	align-items: center;
	align-content: stretch;
  height:83vh;
}

